import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';

import { Service } from '../service';
import { ServicesService } from '../services.service';

import { SpecialtiesService } from '../../+specialties/specialties.service';
import { ToolbarService } from '../../shared/toolbar.service';
import { ToastService } from '../../shared/toast.service';

@Component({
  selector: 'app-service-update',
  templateUrl: './service-update.component.html',
  styleUrls: ['./service-update.component.css']
})
export class ServiceUpdateComponent implements OnInit {
  public service: Service;
  public tags: any[];
  public updateForm: FormGroup;

  constructor(
    private _route: ActivatedRoute,
    private _toolbar: ToolbarService,
    private _servicesService: ServicesService,
    private _specialtiesService: SpecialtiesService,
    private _toastService: ToastService) { }

  ngOnInit() {
    this._toolbar.change({
      title: "Обновить услугу"
    });

    this._specialtiesService.get()
      .subscribe((response) => this.tags = response);
      

    this.updateForm = new FormGroup({
      _id: new FormControl('', [<any>Validators.required]),
      title: new FormControl('', [<any>Validators.required, <any>Validators.minLength(5)]),
      description: new FormControl('', [<any>Validators.required, <any>Validators.minLength(5)]),
      recommendations: new FormControl(),
			price: new FormGroup({
				default: new FormControl(),
				top: new FormControl(),
				kmn: new FormControl()
			}),
			time: new FormControl(),
			tags: new FormControl('', [<any>Validators.required]),
			active: new FormControl('')
    });

    this._route.params.forEach((params: Params) => {
			let id = params['id']; 
			this._servicesService.getOne(id)
				.subscribe(service => {
					this.service = service;
					(<FormGroup>this.updateForm)
            			.patchValue(this.service);
				});
		});
  }

  clearForm(options?: any[]) : void {
    if(this.service != undefined) {
      this.service.clear();
    } else {
      this.service = new Service();
    }
  }

  onSubmit(values, valid) {
    //console.log(values);
    if(valid) {
      this._servicesService.update(values)
        .subscribe((data) => this._toastService.toast("Услуга успешно обновлена!"));
    }
  }

}



