export interface IService {
	_id?: string;
	title: string;
	description: string;
	recommendations: string;
	price: {
		default: number;
		top: number;
		kmn: number;
	};
	time: number;
	tags: string;
	active: boolean;
}

export class Service implements IService {
    _id?: string;
	public title: string;
	public description: string;
	public recommendations: string;
	public price: {
		default: number;
		top: number;
		kmn: number;
	};
	public time: number;
	public tags: string;
	public active: boolean = true;
	
	constructor(object?: IService){
		// if(object != undefined) {
		// 	this.clear();
		// 	if(object._id != undefined) this._id = object._id;
		// 	this.title = object.title;
		// 	this.description = object.description;
		// 	this.recommendations = object.recommendations;
		// 	this.price = object.price;
		// 	this.time = object.time;
		// 	this.tags = object.tags;
		// 	if(object.active != undefined) this.active = object.active;
		// }
	}

	clear() : void {
		this._id = null;
		this.title = null;
		this.description = null;
		this.recommendations = null;
		this.price = {default: null, top: null, kmn: null};
		this.time = null;
		this.tags = null;
		this.active = false;
	}
}



