import { Component, OnInit, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

import { Service } from "../service";
import { ToolbarService } from '../../shared/toolbar.service';
import { ToastService } from '../../shared/toast.service';
import { PaginationService } from '../../shared/pagination.service';
import { ServicesService } from '../services.service';

import { MaterializeAction } from 'angular2-materialize/src';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.css']
})
export class ServicesComponent implements OnInit {
  public services: Service[];
  public currentService: Service;
  public modalActions = new EventEmitter<string|MaterializeAction>();

  constructor(
    private _toolbar: ToolbarService,
    private _toastService: ToastService,
    private _services: ServicesService,
    private _pager: PaginationService,
    private _router: Router
    ) { }

  ngOnInit() {
    this._pager.clear();
    
    this._toolbar.change({
      title: "Список услуг",
      name: "services",
      actions: {
        add: () => this._router.navigate(['services/add'])
      },
      actionsPosition: {
        extend: ['add']
      }
    });
    
    this._services.get()
      .subscribe((response) => {
        this.services = response;
      });
  }

  openModal(item) {
    this.currentService = item;
    this.modalActions.emit({action:"modal",params:['open']});
  }

  closeModal() {
    this.modalActions.emit({action:"modal",params:['close']});
  }

  deleteItem(item) {
    let self = this;

    this._services.remove(item._id)
      .subscribe((data: any) => {
        if(data.status == 'ok') {
          this.services.find((service, index) => {
            if(item._id == service._id) {
              self.currentService = null;
              self.services.splice(index, 1);
              return true;
            }
          });
          this._toastService.toast("Услуга удалена");
        }
      });
  }

}



