import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
//import { MaterialModule } from '@angular/material';
import { MaterializeModule } from "angular2-materialize/src";

import { ServicesComponent } from './services/services.component';
import { ServiceAddComponent } from './service-add/service-add.component';

import { ToolbarService } from '../shared/toolbar.service';
import { SpecialtiesService } from '../+specialties/specialties.service';

import { ServicesService } from './services.service';
import { ServiceUpdateComponent } from './service-update/service-update.component';
 
const SERVICE_ROUTES: Routes = [
  { path: '', component: ServicesComponent },
  { path: 'add', component: ServiceAddComponent },
  { path: 'update/:id', component: ServiceUpdateComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(SERVICE_ROUTES),
    ReactiveFormsModule, FormsModule,
    MaterializeModule
    //MaterialModule.forRoot()
  ],
  declarations: [ServicesComponent, ServiceAddComponent, ServiceUpdateComponent],
  providers: [ServicesService, SpecialtiesService],
  exports: [ServicesComponent]
})
export class ServicesModule { }


